import { Component, forwardRef, Input, OnInit } from '@angular/core';
import { ControlValueAccessor, NG_VALIDATORS, NG_VALUE_ACCESSOR, Validator } from '@angular/forms';

@Component({
  selector: 'ng-birthdate',
  templateUrl: './ng-birthdate.template.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => NgBirthDateComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => NgBirthDateComponent),
      multi: true
    }
  ]
})
export class NgBirthDateComponent implements ControlValueAccessor, Validator, OnInit {

  years: string[];
  months: string[];
  days: string[];

  selectedYear: number;
  selectedMonth: number;
  selectedDay: number;

  onChange: any = () => {};
  onTouched: any = () => {};

  constructor() {
    const currentYear = new Date().getFullYear() + 543;
    this.years = this.generateRange(currentYear, 2470).reverse();
    this.months = [
      '', '', '', '', '', '',
      '', '', '', '', '', ''
    ];
    this.days = this.generateRange(1, 31).reverse();
  }

  ngOnInit(): void {
    this.validateDate();
  }

  generateRange(start: number, end: number): string[] {
    return Array.from({ length: end - start + 1 }, (_, index) => (start + index).toString());
  }

  validateDate(): void {
    this.onChange(this.getDateString());
  }

  getDateString(): string {
    if (this.selectedYear == null || this.selectedMonth == null || this.selectedDay == null) {
      return null;
    }

    const selectedDate = new Date(this.selectedYear, this.selectedMonth - 1, this.selectedDay);

    if (isNaN(selectedDate.getTime())) {
      return null;
    }

    return selectedDate.toISOString().split('T')[0];
  }

  writeValue(value: any): void {
    if (value) {
      const date = new Date(value);
      this.selectedYear = date.getFullYear();
      this.selectedMonth = date.getMonth() + 1;
      this.selectedDay = date.getDate();
    } else {
      this.selectedYear = null;
      this.selectedMonth = null;
      this.selectedDay = null;
    }
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  validate(): { [key: string]: any } {
    const dateString = this.getDateString();

    if (!dateString) {
      return { required: true };
    }

    return null;
  }
}
