import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { DataService } from '../../shared/services/dataService';
import { HttpClient } from '@angular/common/http';

import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AuthService } from '../../shared/auth/auth.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'account-form',
  templateUrl: './LBDUSite.form.component.html'
})

export class OpenAccountFormComponent implements OnInit {
  loading = false;
  submitted = false;
  routeParams = null;
  obj: any = {
    countryIncomeId:203,
    cuntryId:203
  };
  id = 0;
  data= {
    statusList:[],
    titleList:[],
    countryList:[],
    cardTypeList:[],
    maritalStatusList:[],
    investmentObjectiveList:[],
    sourceOfIncomeList:[],
    incomeList:[],
    occupationList:[],
    businessList:[],
    mailingMethodList:[],
    mailingAddressList:[],
    relationshipList:[]
  };

 
  permission: any = {};
  path = "";
  generalList = [];
  options = [
    { value: '1', label: 'Option 1' },
    { value: '2', label: 'Option 2' },
    { value: '3', label: 'Option 3' }
  ];
  
  selectedValues = ['1', '3'];
  constructor(
    private toastr: ToastrService,
    private http: HttpClient,
    private router: Router,
    private activeRoute: ActivatedRoute,
    private service: DataService,
    private authService: AuthService
  ) {
     
  }

   ngOnInit(){
    // adding the lifecycle hook ngOnInit
    //this.service.get('users/list').subscribe(data => {
    //  console.log(data); // using the HttpClient instance, http to call the API then subscribe to the data and display to console
    ///});

    this.routeParams = this.activeRoute.snapshot.params; 
    this.path = this.activeRoute.routeConfig.path.split("/")[0];
    this.getData()
    if(this.routeParams.id != null) this.getRow(this.routeParams.id) ;
  }

  getRow(id: any) {
    this.service.get("LBDUSite.get/"+id)
      .subscribe((response: any) => {
        this.obj = response;
        //this.obj.position = response.position;
      },  error => {
          this.toastr.error(error.message, 'Error!');
     });
  }

  getData() {
    this.service.get("LBDUSite.getdata")
      .subscribe((response: any) => {
        this.data = response;
        //this.obj.position = response.position;
      },  error => {
          this.toastr.error(error.message, 'Error!');
     });
  }
   

  onSubmit() {
 
    this.service.post('LBDUSite.post', this.obj)
      .subscribe((response: any) => {
        console.log(response.data);
        
        this.toastr.success('Create or Update Completed.', 'Succcess');
        this.router.navigate(['/account/list']);
      },
        error => {
           
          if (error.error != null || error.error != undefined)
            this.toastr.error(error.error.Message, 'Error');
          else
            this.toastr.error(error.message, 'Error!');
        });
  }
}
