 
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
 
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
@Injectable()
export class DataService {

    private actionUrl: string;
    private config: string;
 
    constructor(private http: HttpClient) {
   
    }
    
    public getTable(url:string, request:any): Observable<any> {     

        console.log(request);
         
        let page = request.start > 0 ? (request.start / request.length) + 1 : request.start + 1;
        let limit =request.length;
        let filter="";
        let orderBy="";
        request.orders.forEach((order) => {
            orderBy+=`${order.name} ${order.dir.toUpperCase()}`;        
        });
        /*
        request.filters.forEach((filter) => {
            if (filter.value) {
            url += `${filter.name}_like=${filter.value}&`;
            }
        });
        */
        if(request.fullTextFilter){
            filter = request.fullTextFilter;
        }
        
        var data = {
            page:page,
            limit:limit,
            order:orderBy,
            filter:filter
        }
         
        return this.http.post<any>(environment.apiUrl+url, JSON.stringify(data)).pipe(
            map((response) => {
                // modify the response as needed
                return {                             
                recordsTotal: response['total'],
                recordsFiltered: response['total'],
                data: response['data']
                };   
            })
        );

    } 
    public get<T>(url:string): Observable<T> {
        
        return this.http.get<T>(environment.apiUrl+url);
    }

    public getSingle<T>(url:string,id: number): Observable<T> {
        return this.http.get<T>(environment.apiUrl+url + id);
    }

    public post<T>(url:string,data:any): Observable<T> {        
      
        return this.http.post<T>(environment.apiUrl+url, JSON.stringify(data));
    }
    public post2(url: string, data: any) {

      return this.http.post(url, JSON.stringify(data));
    }
    
    public update<T>(url:string,id: number, itemToUpdate: any): Observable<T> {
        return this.http
            .put<T>(environment.apiUrl+url + id, JSON.stringify(itemToUpdate));
    }

    public delete<T>(url:string,id: number): Observable<T> {
        return this.http.get<T>(environment.apiUrl+url +'/'+ id);
    }

 
}

 
